.skills{
    width:100%;
    padding:6rem 1rem;
    background: #000;
}

.card-container{
    max-width: 1140px;
    margin: auto;
    display: grid;
    grid-template-columns: repeat(2,1fr);
    grid-gap: 40px;
}

@media screen and (max-width:640px){
    .card-container{
        max-width: 1140px;
        margin: auto;
       
        grid-template-columns: 1fr;
        
    }
    
}

.card{
    border: 1px solid white;
    color: white;
    text-align: center;
    padding: 1rem;
}

.card:hover{
    background-color: #141414;
}

.card h3{
    font-size: 1.4rem;
    padding: 1rem;
}


.card p{
    padding: 0px 0;
}

.bar{
    border-bottom: 1px solid #eee;
    width: 10%;
    margin: 1.5rem auto;
    display: block;
}


