.header{
    display:flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem;
    position: fixed;
    width: 100%;
    height: 90px;
    z-index: 10;
}

.header-bg{
    
    background-color: rgba(0,0,0,0.7);
    transition: 0.5s;
}   

.nav{
    display: flex;
}

.nav li{
    padding: 0 1rem;
}

.nav li a{
    font-size: 1.2rem;
    font-weight: 500;


}

.ham{
    display: none;
}

@media screen and (max-width:1040px){
    .nav{
        flex-direction: column;
        justify-content: center;
        align-items: center;
        width: 100%;
        height: 100vh;
        background: rgba(0,0,0,0.9);
        position: absolute;
        top: 0;
        left: -100%;
        z-index: -3;
        transition: 0.3s;
    }

    .nav.active{
        left:0
    }

    .nav li{
        padding: 1rem 0;
    }

    .nav li a{
        font-size: 2rem;
    }

    .ham{
        display: initial;
    }
}