@import url('https://fonts.googleapis.com/css2?family=Ibarra+Real+Nova&family=Outfit:wght@100;200;300;400;500;600;700;800;900&display=swap');

*{
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Ibarra Real Nova', serif;
  font-family: 'Outfit', sans-serif;
}

body{
  background-color:black;
}

h1,h2,h3,h4,p,a{
  color: white;
  text-decoration: none;
}

.btn{
  padding: 12px 32px;
  font-size: 1rem;
  text-transform: uppercase;
  background:rgb(9, 139, 85);
  color: #222;
  border:1px solid #fff;
  font-weight: 600;
  cursor:pointer;
}

.btn-light{
  background:transparent;
  color: #fff;
}

.btn:hover{
  background: grey;
  color:#fff;
  transition: 0.3s;
}