.heroimg{
    width:100%;
    background:rgba(0,0,0,0.5);
    height:60vh;
    position: relative;
}

.heading{
    width: 100%;
    height:100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.heroimg::before{
    content: "";
    background:url("https://images.unsplash.com/photo-1617900906639-cab7adceb499?ixlib=rb-4.0.3&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=1470&q=80");
    background-size: cover;
    background-position: center top;
    height: 100%;
    width: 100%;
    position: absolute;
    top: 0px;
    left: 0;
    z-index: -1;
}

.heroimg h1{
    font-size:2.4 rem;
    margin-top: 200px;

}

.heroimg p{
    font-size: 1.4rem;
    text-align: center;
    width: 30%;
   
}

@media screen and(max-width:640px) {
    .heroimg h1{
        font-size: 2rem;
    }
    
}